import React from "react";
import {
  Box, Card, CardContent, Typography, Table, TableBody, TableCell, TableContainer,
  TableHead, TableRow, Paper, Divider, Stack, Button
} from "@mui/material";
import { useLocation } from "react-router-dom";
import { useNavigation } from "../../routes/Navigation";
import { facturarArticulos } from "./Facturar";
import "../../../styles/facturacion/resumenFactura/ResumenFactura.css";

const ResumenFactura = () => {
  const { goToFacturacion } = useNavigation();
  const location = useLocation();
  const facturaData = location.state?.facturaData ?? {};
  const cliente = facturaData.cliente ?? { name: "Desconocido", document: "-", email: "-", ccNit: "" };
  const productos = Array.isArray(facturaData.articulos) ? facturaData.articulos : [];
  const numeroFactura = facturaData.numeroFactura ?? "";
  const docDate = facturaData.docDate ?? "";
  const total = parseFloat(facturaData.total) || 0;

  const handleConfirmation = async () => {
    // Obtener fecha actual del sistema y nmero de factura
    const now = new Date();
    const offset = now.getTimezoneOffset() * 60000; // Convierte minutos a milisegundos
    const fechaEmision = new Date(now - offset).toISOString().replace("T", " ").slice(0, 23);
    const payload = {
      bill: {
        store: parseInt(localStorage.getItem("storeId"), 10),
        docDate: docDate,
        numAtCard: numeroFactura,
        consecutivoApp: parseInt(localStorage.getItem("ConsecutivoFE"), 10),
        horaEmisionApp: fechaEmision,
        vendedorApp: localStorage.getItem("VendedorAPP"),
        docTotal: total,
        tipoDocumento: cliente.ccNit,
        ccNit: cliente.document,
        nombreCliente: cliente.name + " " + cliente.lastName,
        productos: productos
      },
      client: cliente
    };
    console.log(payload);
    await facturarArticulos(payload);
  }

  return (
    <Box className="resumen-factura-container">
      <Typography variant="h3" gutterBottom align="center" className="resumen-factura-titulo">
         Resumen de Factura
      </Typography>

      <Card elevation={3} className="resumen-factura-card">
        <CardContent>
          {/* Informacin del Cliente y Fecha/Nmero de Factura */}
          <Stack direction="row" justifyContent="space-between" alignItems="center" className="resumen-factura-header">
            <Paper elevation={2} className="resumen-factura-cliente">
              <Typography variant="subtitle1"><strong>Cliente:</strong> {cliente.name + " " + cliente.lastName}</Typography>
              <Typography variant="subtitle2"><strong>N Documento:</strong> {cliente.document}</Typography>
              <Typography variant="subtitle2"><strong>Correo Electrnico:</strong> {cliente.email}</Typography>
              <Typography variant="body2"><strong>Fecha de Emisin:</strong> {docDate}</Typography>
              <Typography variant="body2"><strong>Nmero de Factura:</strong> {numeroFactura}</Typography>
            </Paper>
          </Stack>

          {/* Lista de Productos */}
          <TableContainer component={Paper} elevation={2} className="resumen-factura-tabla">
            <Table size="small">
              <TableHead>
                <TableRow>
                  <TableCell><strong>Producto</strong></TableCell>
                  <TableCell align="center"><strong>Cant.</strong></TableCell>
                  <TableCell align="right"><strong>Precio</strong></TableCell>
                  <TableCell align="right"><strong>Total(Impuesto y Descuento)</strong></TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {productos.length > 0 ? (
                  productos.map((producto, index) => {
                    return (
                      <TableRow key={index}>
                        <TableCell>{producto.articulo}</TableCell>
                        <TableCell align="center">{producto.cantidad}</TableCell>
                        <TableCell align="right">${Math.round(producto.precioSinImpuesto)}</TableCell>
                        <TableCell align="right">${Math.round(producto.totalLinea)}</TableCell>
                      </TableRow>
                    );
                  })
                ) : (
                  <TableRow>
                    <TableCell colSpan={4} align="center">No hay productos en la factura.</TableCell>
                  </TableRow>
                )}
              </TableBody>
            </Table>
          </TableContainer>

          {/* Total */}
          <Stack direction="row" justifyContent="space-between" className="resumen-factura-total">
            <Typography variant="h6"><strong>Total:</strong></Typography>
            <Typography variant="h6" color="error"><strong>${Math.round(total)}</strong></Typography>
          </Stack>

          {/* Botones */}
          <Divider sx={{ marginY: 2 }} />
          <Stack direction="row" spacing={2} justifyContent="center" className="resumen-factura-botones">
            <Button variant="contained" color="primary" onClick={handleConfirmation}>Confirmar</Button>
            <Button variant="outlined" color="secondary" onClick={goToFacturacion}>Cancelar</Button>
          </Stack>
        </CardContent>
      </Card>
    </Box>
  );
};

export default ResumenFactura;
