import React, { useState, useEffect } from "react";
import {
  Button, Box, Typography, Input, Divider, Select, Option
} from '@mui/joy';
import { loginUser, consultarTiendas } from "./LoginService";
import { useNavigation, useRedirectIfAuthenticated } from "../routes/Navigation";
import '../../styles/login/LogIn.css';

function LogIn() {
  const [stores, setStores] = useState([]); // Estado para almacenar las tiendas
  const { goToMenuPrincipal } = useNavigation();
  useRedirectIfAuthenticated("/menu");

  const [formData, setFormData] = useState({
    document: "",
    password: "",
    storeName: localStorage.getItem("selectedStore") || "",
    store: parseInt(localStorage.getItem("storeId"), 10) || 0
  });

  // Guardar en localStorage solo cuando cambia la tienda seleccionada
  useEffect(() => {
    if (formData.storeName) {
      localStorage.setItem("selectedStore", formData.storeName);
      localStorage.setItem("storeId", formData.store);
    }
  }, [formData.storeName, formData.store]);

  // Cargar tiendas desde la API
  useEffect(() => {
    const getStores = async () => {
      try {
        const data = await consultarTiendas(); // Llamada a la API
        setStores(data);

        // Si hay una tienda guardada en localStorage, asegurarse de mantener su ID
        if (formData.storeName) {
          const selectedStore = data.find(store => store[1] === formData.storeName);
          if (selectedStore) {
            setFormData(prev => ({
              ...prev,
              store: selectedStore[0]
            }));
          }
        }
      } catch (error) {
        console.error("Error obteniendo las tiendas:", error);
      }
    };

    getStores();
  }, []);

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleStoreChange = (event, newValue) => {
    const selectedStore = stores.find(store => store[1] === newValue);
    if (selectedStore) {
      setFormData({
        ...formData,
        storeName: selectedStore[1], // Nombre de la tienda
        store: selectedStore[0]  // ID de la tienda
      });
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const response = await loginUser(formData);
    if (response) {
      localStorage.setItem("VendedorAPP", response[0]);
      localStorage.setItem("Prefijo", response[1]);
      localStorage.setItem("ConsecutivoFE", response[2]);
      goToMenuPrincipal();
    } else {
      alert(response);
    }
  };

  return (
    <Box className="main-container">
      <Box className="logo-box">
        <img src="/resources/IconoLogIn/MinisoNombre.png" alt="Miniso Nombre" />
      </Box>

      <Box className="login-box">
        <Typography component="h1" className="title">Iniciar sesin</Typography>
        <Divider />
        <Box component="form" noValidate sx={{ mt: 1 }} onSubmit={handleSubmit}>
          <Input
            className="input"
            variant="outlined"
            required
            fullWidth
            id="document"
            name="document"
            autoComplete="document"
            autoFocus
            placeholder="Usuario"
            value={formData.document}
            onChange={handleChange}
          />
          <Input
            className="input"
            variant="outlined"
            required
            fullWidth
            id="password"
            type="password"
            name="password"
            autoComplete="password"
            placeholder="Contrasea"
            value={formData.password}
            onChange={handleChange}
          />
          <Select
            className="input"
            required
            sx={{ width: "100%" }}
            placeholder="Selecciona tu tienda"
            value={formData.storeName}
            onChange={handleStoreChange}
          >
            {stores.length > 0 ? (
              stores.map((store, index) => (
                <Option key={index} value={store[1]}>{store[1]}</Option>
              ))
            ) : (
              <Option disabled>Cargando tiendas...</Option>
            )}
          </Select>
          <Box className="button-box">
            <Button type="submit" fullWidth variant="contained" className="button-login">
              Iniciar sesin
            </Button>
          </Box>
        </Box>
      </Box>
    </Box>
  );
}

export default LogIn;
