import React, { useState } from "react";
import {
	Dialog, DialogTitle, DialogContent, DialogActions, Button, Typography, CircularProgress, Alert, Box
} from "@mui/material";
import { Grid, Input, IconButton, Chip } from "@mui/joy";
import { consultarArticulo } from "./PeticionesBackend";
import AddShoppingCartIcon from "@mui/icons-material/AddShoppingCart";
import SearchIcon from "@mui/icons-material/Search";
import InventoryIcon from "@mui/icons-material/Inventory";
import "../../../styles/facturacion/modalAgregarProducto/ModalAgregarProducto.css";

const ModalAgregarProducto = ({ open, handleClose, agregarProducto, numeroFactura, docDate }) => {
	const [lineaActual, setLineaActual] = useState(0); // Estado para manejar el nmero de lneas

	const inv_fe_AppState = {
		store: parseInt(localStorage.getItem("storeId"), 10),
		numAtCard: numeroFactura,
		lineas: lineaActual, // Inicia con el valor actual de las lneas
		sku: "",
		articulo: "",
		codigoBarras: "",
		cantidad: 1,
		valorImpuesto: 0,
		precioSinImpuesto: 0,
		valorDescuento: 0,
		impuesto: 0,
		totalLinea: 0,
		docDate: docDate
	};

	const [nuevoProducto, setNuevoProducto] = useState(inv_fe_AppState);
	const [loading, setLoading] = useState(false);
	const [error, setError] = useState("");
	const [productoEncontrado, setProductoEncontrado] = useState(false);
	const [errorAgregar, setErrorAgregar] = useState("");

	const obtenerValorImpuesto = (taxRuleName) => {
		switch (taxRuleName) {
			case "IVA 19%":
				return 19;
			case "IVA EXCLUIDO":
				return 0;
			default:
				return 0; // Valor por defecto si no coincide con ninguno
		}
	};
	
	const buscarProductoPorCodigo = async () => {
		if (!nuevoProducto.codigoBarras.trim()) {
			setError("Ingrese un cdigo de barras");
			return;
		}

		setLoading(true);
		setError("");
		setProductoEncontrado(false);
		setErrorAgregar("");

		try {
			const data = await consultarArticulo(nuevoProducto.codigoBarras);
			if (data && data.article) {
				setNuevoProducto(prev => ({
					...prev,
					sku: data.sku,
					articulo: data.article,
					valorImpuesto: data.taxValue,
					precioSinImpuesto: data.priceWithoutTax,
					valorDescuento: data.descuento ?? 0,
					impuesto: obtenerValorImpuesto(data.tax)
				}));

				setProductoEncontrado(true);
			} else {
				setError("Producto no encontrado");
			}
		} catch (err) {
			setError("Error al buscar el producto");
			console.error("Error en la bsqueda:", err);
		} finally {
			setLoading(false);
		}
	};

	const calcularTotal = () => {
		return (
			Number(((nuevoProducto.precioSinImpuesto + nuevoProducto.valorImpuesto) * nuevoProducto.cantidad).toFixed(2))
		);
	};

	const handleChange = (e) => {
		const { name, value } = e.target;
		setNuevoProducto(prev => ({
			...prev,
			[name]: name === "Cantidad" ? Math.max(parseInt(value) || 1, 1) : value
		}));
	};

	const handleSubmit = () => {
		if (!productoEncontrado) {
			setErrorAgregar("Debe buscar un producto vlido antes de agregar.");
			return;
		}

		const productoConLinea = { ...nuevoProducto, lineas: lineaActual, totalLinea: calcularTotal() }; // Asignar el nmero de lnea actual
		agregarProducto(productoConLinea);
		setLineaActual(prev => prev + 1); // Incrementar lnea para el siguiente producto

		handleClose();
		resetForm();
	};

	const resetForm = () => {
		setNuevoProducto({ ...inv_fe_AppState, lineas: lineaActual }); // Mantener Lineas con el valor actual
		setError("");
		setProductoEncontrado(false);
		setErrorAgregar("");
	};

	return (
		<Dialog open={open} onClose={handleClose} fullWidth maxWidth="sm" className="modal-agregar-producto">
			<DialogTitle>
				<Box style={{ display: "flex", alignItems: "center" }}>
					<InventoryIcon />
					<Typography variant="h6" component="h6" sx={{ marginLeft: 1 }}>
						Aadir Producto
					</Typography>
				</Box>
			</DialogTitle>

			<DialogContent dividers className="modal-agregar-producto-contenido">
				<Grid container spacing={2} alignItems="center">
					<Grid xs={9} className="modal-agregar-producto-busqueda">
						<Input
							sx={{ width: "100%" }}
							name="codigoBarras"
							placeholder="Cdigo de Barras"
							value={nuevoProducto.codigoBarras}
							onChange={handleChange}
						/>
					</Grid>
					<Grid xs={3} className="modal-agregar-producto-boton-buscar">
						<IconButton
							onClick={buscarProductoPorCodigo}
							disabled={loading}
							variant="solid"
							color="primary"
							sx={{ width: "100%" }}
						>
							{loading ? <CircularProgress size={20} /> : <SearchIcon />}
						</IconButton>
					</Grid>

					{error && (
						<Grid xs={12} className="modal-agregar-producto-error">
							<Alert severity="error">{error}</Alert>
						</Grid>
					)}

					{productoEncontrado && (
						<>
							<Grid xs={12} className="modal-agregar-producto-detalle">
								<Typography variant="body2">Nombre:</Typography>
								<Chip color="primary">{nuevoProducto.articulo}</Chip>
							</Grid>
							<Grid xs={6} className="modal-agregar-producto-precio">
								<Typography variant="body2">Precio:</Typography>
								<Chip color="success">${Math.round(nuevoProducto.precioSinImpuesto + nuevoProducto.valorImpuesto)}</Chip>
							</Grid>
							<Grid xs={6} className="modal-agregar-producto-impuesto">
								<Typography variant="body2">Impuesto:</Typography>
								<Chip color="warning">{nuevoProducto.impuesto}%</Chip>
							</Grid>
							<Grid xs={6} className="modal-agregar-producto-cantidad">
								<Typography variant="body2">Cantidad:</Typography>
								<Input
									type="number"
									name="cantidad"
									value={nuevoProducto.cantidad}
									onChange={handleChange}
									min="1"
									sx={{ width: "100%" }}
								/>
							</Grid>
							<Grid xs={12} className="modal-agregar-producto-subtotal">
								<Typography variant="h6" color="error" align="right">
									Total: ${Math.round(calcularTotal())}
								</Typography>
							</Grid>
						</>
					)}

					{errorAgregar && (
						<Grid xs={12} className="modal-agregar-producto-error-agregar">
							<Alert severity="error">{errorAgregar}</Alert>
						</Grid>
					)}
				</Grid>
			</DialogContent>

			<DialogActions className="modal-agregar-producto-acciones">
				<Button onClick={handleClose} color="inherit">Cancelar</Button>
				<Button onClick={handleSubmit} variant="contained" color="error" startIcon={<AddShoppingCartIcon />}>
					Agregar
				</Button>
			</DialogActions>
		</Dialog>
	);
};

export default ModalAgregarProducto;
