import React from "react";
import { Box, Button, Typography, Paper } from "@mui/material";
import Grid from "@mui/material/Grid2"; //  Importacin correcta
import { useNavigation } from "../routes/Navigation"; //  Importamos la navegacin
import "../../styles/menuPrincipal/MenuPrincipal.css"; //  Importamos los estilos externos

const MenuPrincipal = () => {
    const { goToConsultaFactura, goToFacturacion, goToConsultaArticulo, goToCerrarSesion } = useNavigation();
    const selectedStore = localStorage.getItem("selectedStore") || "No seleccionada";

    return (
        <Box className="main-container">
            {/*  Bienvenida con el nombre de la tienda */}
            <Typography variant="h6" className="welcome-message" style={{ position: "absolute", top: 10, left: 10 }}>
                Bienvenido a {selectedStore}
            </Typography>

            {/*  Ttulo */}
            <Typography variant="h2" className="title">
                Men Principal
            </Typography>

            {/*  Contenedor del men */}
            <Paper elevation={10} className="menu-box">
                <Grid container spacing={2} className="menu-grid">
                    {/*  Botn 1: Consulta Factura */}
                    <Grid xs={6} className="grid-item">
                        <Button variant="contained" className="menu-button" onClick={goToConsultaFactura}>
                            Consulta Factura
                        </Button>
                    </Grid>

                    {/*  Botn 2: Facturacin */}
                    <Grid xs={6} className="grid-item">
                        <Button variant="contained" className="menu-button" onClick={goToFacturacion}>
                            Facturacin
                        </Button>
                    </Grid>

                    {/*  Botn 3: Consulta Artculo */}
                    <Grid xs={6} className="grid-item">
                        <Button variant="contained" className="menu-button" onClick={goToConsultaArticulo}>
                            Consulta Artculo
                        </Button>
                    </Grid>

                    {/*  Botn 4: Cerrar Sesin */}
                    <Grid xs={6} className="grid-item">
                        <Button variant="outlined" className="menu-button outlined" onClick={goToCerrarSesion}>
                            Cerrar Sesin
                        </Button>
                    </Grid>
                </Grid>
            </Paper>
        </Box>
    );
};

export default MenuPrincipal;
