import { useNavigate } from "react-router-dom";
import { useEffect } from "react";
import { cerrarSesion } from "../login/LoginService";

//  Hook personalizado para redirigir si hay un token guardado
export const useRedirectIfAuthenticated = (path) => {
    const navigate = useNavigate();

    useEffect(() => {
        const token = localStorage.getItem("token");
        if (token) {
            navigate(path);
        }
    }, [navigate, path]);
};

//  Hook para manejar la navegacin
export const useNavigation = () => {
    const navigate = useNavigate();

    return {
        goToLogin: () => navigate("/login"),
        goToMenuPrincipal: () => navigate("/menu"),
        goToConsultaFactura: () => navigate("/consulta-factura"),
        goToFacturacion: () => navigate("/facturacion"),
        goToConsultaArticulo: () => navigate("/consulta-articulo"),
        goToConfirmacion: (facturaData) => navigate("/confirmacion", { state: { facturaData } }),
        goToCerrarSesion: () => {
            cerrarSesion();
            localStorage.clear();
            navigate("/login");
        },
    };
};