import React from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import App from "../../App.js";
import LogIn from "../login/LogIn";
import ConsultaArticulo from "../consultaArticulo/ConsultaArticulo.jsx";
import ConsultaFactura from "../consultaFactura/ConsultaFactura.jsx";
import MenuPrincipal from "../menuPrincipal/MenuPrincipal.jsx";
import FacturacionPOS from "../facturacion/facturacionPOS/Facturacion.jsx";
import ResumenFactura from "../facturacion/resumenFactura/ResumenFactura.jsx";

class RoutesManager extends React.Component {
    render() {
        return (
            <Router>
                <Routes>
                    <Route path="/" element={<App />} />
                    <Route path="/login" element={<LogIn />} />
                    <Route path="/menu" element={<MenuPrincipal />} />
                    <Route path="/consulta-articulo" element={<ConsultaArticulo />} />
                    <Route path="/consulta-factura" element={<ConsultaFactura />} />
                    <Route path="/facturacion" element={<FacturacionPOS />} />
                    <Route path="/confirmacion" element={<ResumenFactura />} />
                </Routes>
            </Router>
        );
    }
}

export default RoutesManager;
