import React from 'react';
import { Box, Button } from '@mui/joy';  // Importamos los componentes necesarios
import { useNavigation } from "../routes/Navigation"; //  Importamos la navegacin
import '../../styles/inicio/Inicio.css';  // Importar el archivo CSS

function Inicio() {
    const { goToLogin } = useNavigation();
    return (
        <Box className="main-container">  {/* Usamos Container para envolver todo */}
            <Box className="logo-box">  {/* Box para el logo */}
                <img
                    src="/resources/IconoInicio/MinisoLogo.png" // URL correcta de la imagen
                    alt="Logo Miniso"
                />
            </Box>
            <Box className="button-box">  {/* Box para el botn */}
                <Button
                    variant="contained"  // Estilo del botn
                    color="primary"      // Color del botn
                    fullWidth             // Hace que el botn ocupe todo el ancho disponible
                    onClick={goToLogin}
                >
                    Iniciar Sesin
                </Button>
            </Box>
        </Box>
    );
}

export default Inicio;