import axios from "axios";

// Funcin para determinar la URL base de la API
const getApiBaseUrl = () => {
	// Detectar si estamos en desarrollo o produccin
	const isDevelopment = window.location.hostname === 'localhost' || window.location.hostname === '127.0.0.1';
	
	// Si estamos en desarrollo local, usar localhost
	if (isDevelopment) {
	  return "http://localhost:8080";
	}
	
	// En otros casos, usar la IP del servidor o una URL relativa
	return window.location.hostname === '192.168.84.17' 
	  ? "http://192.168.84.17:8080"  // Si accedemos desde la misma IP
	  : `${window.location.protocol}//${window.location.hostname}:8080`; // Usar la hostname actual
  };
  
const API_URL = getApiBaseUrl();

export const buscarFactura = async (id) => {
  const response = await axios.get(`${API_URL}/bill/${id}`, {
    withCredentials: true,
    headers: {
      'X-Tienda': localStorage.getItem("selectedStore") // Reemplaza con el valor adecuado
    }
  });
  const data = response.data; // Suponiendo que data es el array de arrays

  // Aqu guardaremos la salida
  const resultado = {
    indice0: null,
    indice1: null,
    indice2: null,
    indice3: null,
    indice4: null,
    indice5: null,
    indice6: null,
    items: []
  };

  // Recorremos cada subarray (cada factura)
  data.forEach((factura, index) => {
    // Solo en la primera iteracin (index === 0) tomamos los 5 primeros campos
    if (index === 0) {
      resultado.indice0 = factura[0];
      resultado.indice1 = factura[1];
      resultado.indice2 = factura[2];
      resultado.indice3 = factura[3];
      resultado.indice4 = factura[4];
      resultado.indice5 = factura[5];
      resultado.indice6 = factura[6];
    }

    // Si deseas un objeto, podras hacerlo as:
    const item = {
      indice5: factura[7],
      indice6: factura[8],
      indice7: factura[9],
      indice8: factura[10],
      indice9: factura[11]
    };

    // O si prefieres guardarlo tal cual como array:
    // const item = [...itemValues];

    // Finalmente, agregamos el item al array de items
    resultado.items.push(item);
  });
  if (resultado.items.length === 0) {
    throw new Error("No se encontr la factura");
  }
  return resultado;
};
