import React, { useState } from "react";
import {
    Box, TextField, Typography, Paper, Table, TableBody, TableCell,
    TableContainer, TableHead, TableRow, Button, Divider
} from "@mui/material";
import { Grid } from "@mui/joy";
import ManageSearchIcon from '@mui/icons-material/ManageSearch';
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';
import { buscarFactura } from "./FacturaService";
import { useNavigation } from "../routes/Navigation"; //  Importamos la navegacin
import "../../styles/consultaFactura/ConsultaFactura.css";

const ConsultaFactura = () => {
    const [numeroFactura, setNumeroFactura] = useState("");
    const [facturas, setFacturas] = useState([]);
    const [error, setError] = useState("");

    const { goToMenuPrincipal, goToLogin } = useNavigation();

    const handleBuscarFactura = async () => {
        try {
            const resultado = await buscarFactura(numeroFactura);
            setFacturas([resultado]);
            setError(""); //  Si no hay error, limpiamos el mensaje de error
        } catch (error) {
            setFacturas([]); //  Limpiamos los datos de la factura si hay error
            if (error.response && error.response.status === 401) {
                goToLogin();
            } else {
                setError(" " + (error.message || "Error desconocido al buscar la factura."));
            }
        }
    };

    return (
        <Box className="main-container">
            <Typography variant="h3" className="factura-title">
                 Consulta de Factura
            </Typography>

            <Paper elevation={10} className="factura-box">
                <Box className="search-container">
                    <Button variant="contained" className="back-button" startIcon={<ArrowBackIosNewIcon />} onClick={goToMenuPrincipal}>
                        Regresar
                    </Button>
                    <TextField
                        label="Nmero de Factura"
                        variant="outlined"
                        value={numeroFactura}
                        onChange={(e) => setNumeroFactura(e.target.value)}
                        className="search-input"
                    />
                    <Button variant="contained" className="search-button" onClick={handleBuscarFactura} startIcon={<ManageSearchIcon />}>
                        Buscar
                    </Button>
                </Box>

                {/*  Mostrar error si existe */}
                {error && (
                    <Grid item xs={12} className="consulta-grid-item">
                        <Typography variant="h6" className="consulta-error">{error}</Typography>
                    </Grid>
                )}

                {/*  Solo mostrar la factura si no hay error y hay datos */}
                {!error && facturas.length > 0 && facturas.map((factura, facturaIndex) => (
                    <Paper key={facturaIndex} elevation={6} className="factura-container">
                        <Grid container spacing={2}>
                            <Grid item xs={12} sm={6}>
                                <TableContainer>
                                    <Table size="small">
                                        <TableBody>
                                            <TableRow><TableCell><strong>Fecha:</strong></TableCell><TableCell>{factura.indice1 || "N/A"}</TableCell></TableRow>
                                            <TableRow><TableCell><strong>Tienda:</strong></TableCell><TableCell>{factura.indice4 || "N/A"}</TableCell></TableRow>
                                            <TableRow><TableCell><strong>Cajero:</strong></TableCell><TableCell>{factura.indice5 || "N/A"}</TableCell></TableRow>
                                        </TableBody>
                                    </Table>
                                </TableContainer>
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <TableContainer>
                                    <Table size="small">
                                        <TableBody>
                                            <TableRow>
                                                <TableCell><strong>Cliente:</strong></TableCell>
                                                <TableCell>{factura.indice6?.trim() || "N/A"}</TableCell>
                                            </TableRow>
                                            <TableRow>
                                                <TableCell><strong>Nmero Documento:</strong></TableCell>
                                                <TableCell>{factura.indice3?.trim() || "N/A"}</TableCell>
                                            </TableRow>
                                            <TableRow className="total-row">
                                                <TableCell colSpan={2} align="right">
                                                    <Box className="total-container">
                                                        <Typography variant="body1" className="total-label">Total:</Typography>
                                                        <Typography variant="h5" className="total-amount">${factura.indice2}</Typography>
                                                    </Box>
                                                </TableCell>
                                            </TableRow>
                                        </TableBody>
                                    </Table>
                                </TableContainer>
                            </Grid>
                        </Grid>
                        <Divider sx={{ marginY: 2 }} />
                        <TableContainer component={Paper} className="table-container" style={{ maxHeight: 250, overflowY: "auto" }}>
                            <Table stickyHeader size="small">
                                <TableHead>
                                    <TableRow>
                                        <TableCell className="header-cell">SKU</TableCell>
                                        <TableCell className="header-cell">Cdigo de Barras</TableCell>
                                        <TableCell className="header-cell">Artculo</TableCell>
                                        <TableCell className="header-cell">Cantidad</TableCell>
                                        <TableCell className="header-cell">Precio</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {factura.items.map((item, index) => (
                                        <TableRow key={index} className="table-row">
                                            <TableCell>{item.indice5 || "N/A"}</TableCell>
                                            <TableCell>{item.indice6 || "N/A"}</TableCell>
                                            <TableCell>{item.indice7 || "N/A"}</TableCell>
                                            <TableCell>{item.indice8 || 0}</TableCell>
                                            <TableCell>${item.indice9 ? item.indice9.toFixed(2) : "0.00"}</TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Paper>
                ))}
            </Paper>
        </Box>
    );
};

export default ConsultaFactura;
