import React, { useState, useEffect } from "react";
import {Box, Card, CardContent, Button, TextField, Table, TableBody, TableCell, TableContainer,
  TableHead, TableRow, Paper, Typography, IconButton, useMediaQuery, useTheme, Divider, Stack
} from "@mui/material";
import { Grid } from "@mui/joy";
import AddShoppingCartIcon from "@mui/icons-material/AddShoppingCart";
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';
import SearchIcon from "@mui/icons-material/Search";
import DeleteOutlineIcon from "@mui/icons-material/DeleteOutline";
import UpdateIcon from "@mui/icons-material/Update";
import ReceiptLongIcon from "@mui/icons-material/ReceiptLong";
import { useNavigation } from "../../routes/Navigation.js"; //  Importamos la navegacin
import { calcularTotal, removeItem } from "./Utils.js";
import { consultarCliente } from "./PeticionesBackend.js";
import ModalAgregarProducto from "./ModalAgregarProducto.jsx"; //  Importamos el modal
import ModalActualizarCliente from "./ModalActualizarCliente.jsx";
import "../../../styles/facturacion/Facturacion.css";

const FacturacionPOS = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  const [cliente, setCliente] = useState({ document: "", ccNit: "", name: "", lastName: "", email: "" });
  const [numeroFactura, setNumeroFactura] = useState(
    `${localStorage.getItem("Prefijo")}-${localStorage.getItem("ConsecutivoFE")}`
  );const [articulos, setArticulos] = useState([]);
  const [modalAbiertoCliente, setModalAbiertoCliente] = useState(false);
  const [modalAbiertoProducto, setModalAbiertoProducto] = useState(false);
  const [mensajeCliente, setMensajeCliente] = useState("");
  const docDate = new Date().toLocaleDateString('sv-SE');

  const { goToMenuPrincipal, goToConfirmacion, goToLogin } = useNavigation();

  // Funciones para manejar el modal de producto
  const abrirModalProducto = () => setModalAbiertoProducto(true);
  const cerrarModalProducto = () => setModalAbiertoProducto(false);

  // Funciones para manejar el modal de cliente
  const abrirModalCliente = () => setModalAbiertoCliente(true);
  const cerrarModalCliente = () => setModalAbiertoCliente(false);

  useEffect(() => {
    const actualizarNumeroFactura = () => {
      setNumeroFactura(`${localStorage.getItem("Prefijo")}-${localStorage.getItem("ConsecutivoFE")}`);
    };

    window.addEventListener("storage", actualizarNumeroFactura);
    
    return () => {
      window.removeEventListener("storage", actualizarNumeroFactura);
    };
  }, []);

  //  Maneja la consulta al backend
  const handleClienteBusqueda = async (document) => {
    try {
      const data = await consultarCliente(document);
      if (data) {
        localStorage.setItem("oldClient", JSON.stringify(data));
        setCliente({
          document: data.document || "",
          ccNit: data.typeId.trim() || "",
          name: data.name || "No disponible",
          lastName: data.lastName || "No disponible",
          email: data.email || "No disponible",
        });
        setMensajeCliente(""); // Limpiar mensaje si se encuentra el cliente
      } else {
        setCliente({ document: "", ccNit: "", name: "", lastName: "", email: "" });
        setMensajeCliente("Cliente no encontrado");
      }
    } catch (error) {
      console.error("Error al buscar el cliente:", error);

      if (error.response && error.response.status === 401) {
        goToLogin();
      } else {
        setCliente({ document: "", ccNit: "", name: "", lastName: "", email: "" });
        setMensajeCliente("Error al buscar el cliente");
      }
    }
  };

  // Funcin para agregar un nuevo producto a la lista
  const agregarProducto = (producto) => {
    setArticulos([...articulos, producto]);
  };

  // Renderizado condicional para mviles
  const renderMobileProductList = () => (
    <Stack spacing={2} sx={{ mt: 2 }}>
      {articulos.length > 0 ? (
        articulos.map((item, index) => (
          <Paper
            key={index}
            elevation={2}
            className="product-card"
            sx={{ p: 2 }}
          >
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
              <Typography variant="subtitle1" fontWeight="bold">{item.Articulo}</Typography>
              <IconButton
                size="small"
                className="delete-button"
                onClick={() => removeItem(index, articulos, setArticulos)}
              >
                <DeleteOutlineIcon />
              </IconButton>
            </Box>
            <Divider sx={{ my: 1 }} />
            <Grid container spacing={1}>
              <Grid item xs={4}>
                <Typography variant="body2">Cantidad:</Typography>
                <Typography variant="body1" fontWeight="medium">{item.cantidad}</Typography>
              </Grid>
              <Grid item xs={4}>
                <Typography variant="body2">Precio Unitario:</Typography>
                <Typography variant="body1" fontWeight="medium">${Math.round(item.precioSinImpuesto)}</Typography>
              </Grid>
              <Grid item xs={4}>
                <Typography variant="body2">Descuento:</Typography>
                <Typography variant="body1" fontWeight="medium">{item.valorDescuento}%</Typography>
              </Grid>
              <Grid item xs={4}>
                <Typography variant="body2">Impuesto:</Typography>
                <Typography variant="body1" fontWeight="medium">{item.impuesto}%</Typography>
              </Grid>
              <Grid item xs={8}>
                <Typography variant="body2">Total:</Typography>
                <Typography variant="body1" fontWeight="medium">${Math.round(item.totalLinea)}</Typography>
              </Grid>
            </Grid>
          </Paper>
        ))
      ) : (
        <Paper sx={{ p: 2, textAlign: 'center' }}>
          <Typography>No hay productos</Typography>
        </Paper>
      )}
    </Stack>
  );

  // Renderizado para tablets y desktop
  const renderProductTable = () => (
    <TableContainer className="table-container" component={Paper} elevation={3}>
      <Table stickyHeader>
        <TableHead>
          <TableRow>
            <TableCell className="header-cell">Artculo</TableCell>
            <TableCell className="header-cell">Cantidad</TableCell>
            <TableCell className="header-cell">Precio Unitario</TableCell>
            <TableCell className="header-cell">Descuento</TableCell>
            <TableCell className="header-cell">Impuesto</TableCell>
            <TableCell className="header-cell">Total</TableCell>
            <TableCell className="header-cell" />
          </TableRow>
        </TableHead>
        <TableBody>
          {articulos.length > 0 ? (
            articulos.map((item, index) => (
              <TableRow key={index} hover className="table-row">
                <TableCell>{item.articulo}</TableCell>
                <TableCell>{item.cantidad}</TableCell>
                <TableCell>${Math.round(item.precioSinImpuesto)}</TableCell>
                <TableCell>{item.valorDescuento}%</TableCell>
                <TableCell>{item.impuesto}%</TableCell>
                <TableCell>${Math.round(item.totalLinea)}</TableCell>
                <TableCell>
                  <IconButton
                    className="delete-button"
                    onClick={() => removeItem(index, articulos, setArticulos)}
                  >
                    <DeleteOutlineIcon />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))
          ) : (
            <TableRow>
              <TableCell colSpan={7} align="center">No hay productos</TableCell>
            </TableRow>
          )}
        </TableBody>
      </Table>
    </TableContainer>
  );

  return (
    <Box className="main-container">
      {/* Ttulo */}
      <Typography variant="h3" className="factura-title">
         Facturacin POS
      </Typography>

      {/* Contenedor principal */}
      <Card className="factura-box">
        <CardContent sx={{ p: { xs: 1, sm: 2, md: 3 } }}>
          {/* Botn regresar y Aadir Producto */}
          <Box sx={{
            mb: 2,
            display: 'flex',
            justifyContent: 'space-between',
            flexDirection: { xs: 'column', sm: 'row' },
            gap: { xs: 1, sm: 0 }
          }}>
            <Button
              className="back-button"
              variant="contained"
              color="error"
              startIcon={<ArrowBackIosNewIcon />}
              onClick={goToMenuPrincipal}
              size={isMobile ? "small" : "medium"}
              sx={{ width: { xs: '100%', sm: 'auto' } }}
            >
              Regresar
            </Button>
            <Button
              className="add-product-button"
              variant="contained"
              color="error"
              startIcon={<AddShoppingCartIcon />}
              onClick={abrirModalProducto}
              size={isMobile ? "small" : "medium"}
              sx={{ width: { xs: '100%', sm: 'auto' } }}
            >
              Aadir Producto
            </Button>
          </Box>

          <Grid container spacing={2} alignItems="flex-start">
            {/* Cuadro de Bsqueda y Datos del Cliente */}
            <Grid item xs={12} md={3}>
              <Paper className="search-box" elevation={3} sx={{ padding: 2 }}>
                <TextField
                  className="search-input"
                  label="Documento del Cliente"
                  variant="outlined"
                  fullWidth
                  value={cliente.document}
                  onChange={(e) => setCliente({ ...cliente, document: e.target.value })}
                  sx={{ my: 1 }}
                  size={isMobile ? "small" : "medium"}
                />
                <Stack spacing={1}>
                  <Button
                    className="search-button"
                    variant="contained"
                    color="error"
                    startIcon={<SearchIcon />}
                    fullWidth
                    onClick={() => handleClienteBusqueda(cliente.document)}
                    size={isMobile ? "small" : "medium"}
                  >
                    Buscar Cliente
                  </Button>
                  <Button
                    className="update-button"
                    variant="contained"
                    color="error"
                    startIcon={<UpdateIcon />}
                    fullWidth
                    onClick={abrirModalCliente}
                    size={isMobile ? "small" : "medium"}
                  >
                    Actualizar Cliente
                  </Button>
                </Stack>
              </Paper>

              {/* Datos del Cliente */}
              <Paper
                className="cliente-info"
                elevation={3}
                sx={{
                  padding: 2,
                  marginTop: 2,
                  display: 'flex',
                  flexDirection: 'column',
                  gap: 0.5
                }}
              >
                {mensajeCliente ? (
                  <Typography color="error" variant="body1">{mensajeCliente}</Typography>
                ) : (
                  <>
                    <Typography sx={{ fontSize: { xs: '0.875rem', md: '1rem' } }}>
                      <strong>Nombre:</strong> {cliente?.name || "No disponible"}
                    </Typography>
                    <Typography sx={{ fontSize: { xs: '0.875rem', md: '1rem' } }}>
                      <strong>Apellido:</strong> {cliente?.lastName || "No disponible"}
                    </Typography>
                    <Typography sx={{ fontSize: { xs: '0.875rem', md: '1rem' } }}>
                      <strong>Documento:</strong> {cliente?.document || "No disponible"}
                    </Typography>
                    <Typography sx={{ fontSize: { xs: '0.875rem', md: '1rem' }, wordBreak: 'break-word' }}>
                      <strong>Correo:</strong> {cliente?.email || "No disponible"}
                    </Typography>
                  </>
                )}
              </Paper>
            </Grid>

            {/* Tabla de productos */}
            <Grid item xs={12} md={9}>
              <Box className="products-section" sx={{ mt: { xs: 0, md: -2 } }}>
                {isMobile ? renderMobileProductList() : renderProductTable()}

                {/* Total y botn Facturar */}
                <Box
                  className="total-container"
                  sx={{
                    flexDirection: { xs: 'column', sm: 'row' },
                    gap: { xs: 1, sm: 0 },
                    mt: { xs: 2, md: -1 },
                    padding: { xs: 2, sm: 3 }
                  }}
                >
                  <Typography variant="h6" sx={{ fontSize: { xs: '1rem', md: '1.25rem' } }}>
                    <strong>Total:</strong>
                  </Typography>
                  <Typography variant="h6" sx={{
                    fontSize: { xs: '1.25rem', md: '1.5rem' },
                    color: '#f44336',
                    textAlign: { xs: 'center', sm: 'left' }
                  }}>
                    <strong>${Math.round(calcularTotal(articulos))}</strong>
                  </Typography>
                  <Button
                    className="facturar-button"
                    variant="contained"
                    color="error"
                    startIcon={<ReceiptLongIcon />}
                    size={isMobile ? "medium" : "large"}
                    disabled={
                      !cliente.document || 
                      !cliente.name || 
                      !cliente.lastName || 
                      !cliente.email || 
                      !articulos || 
                      articulos.length === 0
                    }
                    onClick={() => goToConfirmacion({ docDate, numeroFactura, cliente, articulos, total: calcularTotal(articulos) })}
                    sx={{
                      width: { xs: '100%', sm: 'auto' },
                      py: { xs: 1, md: 1.5 },
                      ml: { xs: 0, sm: 'auto' }
                    }}
                  >
                    Facturar
                  </Button>
                </Box>
              </Box>
            </Grid>
          </Grid>
        </CardContent>
      </Card>

      {/* Modal para aadir productos */}
      <ModalAgregarProducto
        open={modalAbiertoProducto}
        handleClose={cerrarModalProducto}
        agregarProducto={agregarProducto}
        numeroFactura={numeroFactura}
        docDate={docDate}
      />

      {/* Modal para actualizar cliente */}
      <ModalActualizarCliente
        open={modalAbiertoCliente}
        handleClose={cerrarModalCliente}
        cliente={cliente}
      />
    </Box>
  );
};

export default FacturacionPOS;