import axios from "axios";

// Funcin para determinar la URL base de la API
const getApiBaseUrl = () => {
	// Detectar si estamos en desarrollo o produccin
	const isDevelopment = window.location.hostname === 'localhost' || window.location.hostname === '127.0.0.1';
	
	// Si estamos en desarrollo local, usar localhost
	if (isDevelopment) {
	  return "http://localhost:8080";
	}
	
	// En otros casos, usar la IP del servidor o una URL relativa
	return window.location.hostname === '192.168.84.17' 
	  ? "http://192.168.84.17:8080"  // Si accedemos desde la misma IP
	  : `${window.location.protocol}//${window.location.hostname}:8080`; // Usar la hostname actual
  };
  
const API_URL = getApiBaseUrl();

export const consultarArticulo = async (id) => {
    try {
        const response = await axios.get(`${API_URL}/consultaArticulo/${id}`, {
            withCredentials: true,
            headers: {
              'X-Tienda': localStorage.getItem("selectedStore") // Reemplaza con el valor adecuado
            }
        });
        return response.data[0]; // Devuelve los datos del artculo
    } catch (error) {
        console.error("Error al consultar el artculo:", error);
        throw new Error("Artculo no encontrado"); // Manejo del error
    }
};

export const consultarCliente = async (id) => {
    const response = await axios.get(`${API_URL}/client/${id}`, {
        withCredentials: true
    });
    return response.data; // Devuelve los datos del artculo
};

export const actualizarCliente = async (clienteData) => {
    // Guardar en localStorage antes de enviar la peticin
    const oldClient = JSON.parse(localStorage.getItem("oldClient")) || null;

    console.log("Datos enviados al backend:", { clienteData, oldClient });

    const response = await axios.put(
        `${API_URL}/client/update`,
        [oldClient, clienteData], // Enviar como lista
        {
            withCredentials: true,
            headers: {
                "Content-Type": "application/json",
            },
        }
    );
    return response.data;
};