import axios from "axios";

// Funcin para determinar la URL base de la API
const getApiBaseUrl = () => {
	// Detectar si estamos en desarrollo o produccin
	const isDevelopment = window.location.hostname === 'localhost' || window.location.hostname === '127.0.0.1';
	
	// Si estamos en desarrollo local, usar localhost
	if (isDevelopment) {
	  return "http://localhost:8080";
	}
	
	// En otros casos, usar la IP del servidor o una URL relativa
	return window.location.hostname === '192.168.84.17' 
	  ? "http://192.168.84.17:8080"  // Si accedemos desde la misma IP
	  : `${window.location.protocol}//${window.location.hostname}:8080`; // Usar la hostname actual
  };
  
const API_URL = getApiBaseUrl();

export const loginUser = async (formData) => {
	try {
		const response = await axios.post(
			`${API_URL}/login`,
			formData,
			{
				headers: { "Content-Type": "application/json" },
				withCredentials: true, //  Enviar y recibir cookies HTTP-Only
			}
		);
		return response.data;
	} catch (error) {
		console.error( error);
	}
};

export const cerrarSesion = async () => {
	try {
		const response = await axios.post(
			`${API_URL}/logout`,
			{
				withCredentials: true
			}
		);
		console.log(response);
		return response.data;
	} catch (error) {
		console.error("Error al cerrar sesin:", error);
	}
};

export const consultarTiendas = async () => {
	try {
		const response = await axios.get(`${API_URL}/tenant/stores`);
		return response.data || []; // Devuelve los datos del artculo
	} catch (error) {
		console.log(error);
		return [];
	}
};

