import React, { useState } from "react";
import { Box, Button, Typography, Paper, TextField } from "@mui/material";
import { Grid } from "@mui/joy";
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';
import { useNavigation } from "../routes/Navigation"; //  Importamos la navegacin
import "../../styles/consultaArticulo/ConsultaArticulo.css";
import { consultarArticulo } from "./ConsultaArticulo"; //  Importamos el servicio

const ConsultaArticulo = () => {
  const [codigo, setCodigo] = useState("");
  const [articulo, setArticulo] = useState(null);
  const [error, setError] = useState("");

  const { goToMenuPrincipal, goToLogin } = useNavigation();

  //  Maneja la consulta al backend
  const handleConsulta = async () => {
    try {
      const data = await consultarArticulo(codigo);
      setArticulo(data);
      setError("");
    } catch (error) {
      console.error("Error al buscar el cliente:", error);
      if (error.response && error.response.status === 401) {
        console.log("Redirigiendo al login...");
        goToLogin();
      } else {
        setArticulo(null);
        setError("Artculo no encontrado");
      }
    }
  };

  return (
    <Box className="consulta-container">
      <Typography variant="h3" className="consulta-title"> Consulta de Artculo</Typography>

      <Paper elevation={10} className="consulta-box">
        <Grid container spacing={2} className="consulta-grid">
          <Grid xs={12} className="consulta-grid-item">
            <TextField
              label="Cdigo de Barras"
              variant="outlined"
              className="consulta-input"
              value={codigo}
              onChange={(e) => setCodigo(e.target.value)}
            />
          </Grid>

          {/*  Botones alineados */}
          <Grid xs={12} className="consulta-grid-item button-group">
            <Button variant="contained" className="back-button" startIcon={<ArrowBackIosNewIcon />} onClick={goToMenuPrincipal}>
              Regresar
            </Button>

            <Button variant="contained" className="consulta-button" onClick={handleConsulta}>
               Consultar
            </Button>
          </Grid>

          {articulo && (
            <Grid xs={12} className="consulta-grid-item">
              <Paper elevation={5} className="consulta-result-box">
                <Typography variant="h6"><strong> SKU:</strong> {articulo.sku}</Typography>
                <Typography variant="h6"><strong> Nombre:</strong> {articulo.article}</Typography>
                <Typography variant="h6"><strong> Departamento:</strong> {articulo.deptName}</Typography>
                <Typography variant="h6"><strong> Descuento:</strong> {articulo.descuento || "No disponible"}</Typography>
              </Paper>
            </Grid>
          )}

          {error && (
            <Grid xs={12} className="consulta-grid-item">
              <Typography variant="h6" className="consulta-error"> {error}</Typography>
            </Grid>
          )}
        </Grid>
      </Paper>
    </Box>
  );
};

export default ConsultaArticulo;
