import React, { useState, useEffect } from "react";
import {
  Dialog, DialogTitle, DialogContent, DialogActions, Button, Typography, Alert
} from "@mui/material";
import { Grid, Input } from "@mui/joy";
import { actualizarCliente } from "./PeticionesBackend";
import EditIcon from "@mui/icons-material/Edit";
import PersonIcon from "@mui/icons-material/Person";
import "../../../styles/facturacion/modalActualizarCliente/ModalActualizarCliente.css";

const ModalActualizarCliente = ({ open, handleClose, cliente }) => {
  const { gender, idAptos, typeId } = JSON.parse(localStorage.getItem("oldClient")) || "";

  const [clienteData, setClienteData] = useState({
    name: cliente?.name || "",
    lastName: cliente?.lastName || "",
    email: cliente?.email || "",
    document: cliente?.document || "",
    gender: gender || "",
    idAptos: idAptos || "",
    typeId: typeId || ""
  });
  const [confirmDialogOpen, setConfirmDialogOpen] = useState(false);
  const [responseMessage, setResponseMessage] = useState(null);

  useEffect(() => {
    if (cliente) {
      setClienteData({
        name: cliente.name || "",
        lastName: cliente.lastName || "",
        email: cliente.email || "",
        document: cliente.document || "",
        gender: gender || "",
        idAptos: idAptos || "",
        typeId: typeId || ""
      });
    }
  }, [cliente, gender, idAptos, typeId]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setClienteData((prevState) => ({ ...prevState, [name]: value }));
  };

  const handleConfirmUpdate = () => {
    if (!clienteData.name || !clienteData.email || !clienteData.document) {
      setResponseMessage({ type: "error", text: "Por favor, completa todos los campos obligatorios." });
      return;
    }
    setConfirmDialogOpen(true);
  };

  const handleUpdateCliente = async () => {
    if (!clienteData.name || !clienteData.email || !clienteData.document) {
      setResponseMessage({ type: "error", text: "Por favor, completa todos los campos obligatorios." });
      return;
    }

    try {
      const response = await actualizarCliente(clienteData);

      if (response.toLowerCase().includes("error")) {
        setResponseMessage({ type: "error", text: response });
      } else {
        setResponseMessage({ type: "success", text: response });
      }
    } catch (error) {
      setResponseMessage({ type: "error", text: "Error al actualizar el cliente. Intenta nuevamente." });
    }

    setConfirmDialogOpen(false);
  };

  const handleCloseConfirmationDialog = () => {
    setResponseMessage(null);
    handleClose();
  };

  return (
    <>
      <Dialog
        open={open}
        onClose={handleClose}
        fullWidth
        maxWidth="sm"
        className="modal-actualizar-cliente"
        aria-hidden={!open}
      >
        <DialogTitle className="MuiDialogTitle-root">
          <PersonIcon className="modal-icon" />
          <Typography component="span" variant="h6">Actualizar Cliente</Typography>
        </DialogTitle>
        <DialogContent dividers>
          {cliente.documento === "" ? (
            <Alert severity="warning">No hay cliente seleccionado.</Alert>
          ) : (
            <Grid container spacing={2}>
              <Grid item xs={12} className="input-group">
                <Typography variant="body2">Nombre:</Typography>
                <Input className="input-field" sx={{ width: "100%" }} name="name" value={clienteData.name} onChange={handleChange} />
              </Grid>
              <Grid item xs={12} className="input-group">
                <Typography variant="body2">Apellido:</Typography>
                <Input className="input-field" sx={{ width: "100%" }} name="lastName" value={clienteData.lastName} onChange={handleChange} />
              </Grid>
              <Grid item xs={12} className="input-group">
                <Typography variant="body2">Email:</Typography>
                <Input className="input-field" sx={{ width: "100%" }} name="email" value={clienteData.email} onChange={handleChange} />
              </Grid>
            </Grid>
          )}
        </DialogContent>
        <DialogActions className="modal-buttons">
          <Button onClick={handleClose} className="cancel-button">Cancelar</Button>
          <Button
            onClick={handleConfirmUpdate}
            className="update-button"
            startIcon={<EditIcon />}
            disabled={!clienteData.name || !clienteData.email || !clienteData.document}
          >
            Actualizar
          </Button>
        </DialogActions>
      </Dialog>

      {/* Modal de Confirmacin */}
      <Dialog
        open={confirmDialogOpen}
        onClose={() => setConfirmDialogOpen(false)}
        fullWidth
        maxWidth="xs"
        className="modal-actualizar-cliente"
        aria-hidden={!confirmDialogOpen}
      >
        <DialogTitle className="MuiDialogTitle-root">Confirmar Actualizacin</DialogTitle>
        <DialogContent>
          <Typography>Ests seguro de que deseas actualizar este cliente?</Typography>
          <Typography><strong>Nombre:</strong> {clienteData.name}</Typography>
          <Typography><strong>Apellido:</strong> {clienteData.lastName}</Typography>
          <Typography><strong>Email:</strong> {clienteData.email}</Typography>
          <Typography><strong>Documento:</strong> {clienteData.document}</Typography>
        </DialogContent>
        <DialogActions className="modal-buttons">
          <Button onClick={() => setConfirmDialogOpen(false)} className="cancel-button">Cancelar</Button>
          <Button onClick={handleUpdateCliente} className="update-button">Confirmar</Button>
        </DialogActions>
      </Dialog>

      {/* Mostrar mensaje de respuesta */}
      {responseMessage && (
        <Dialog open={Boolean(responseMessage)} onClose={() => handleCloseConfirmationDialog()} fullWidth maxWidth="xs" className="modal-actualizar-cliente">
          <DialogTitle>Resultado</DialogTitle>
          <DialogContent>
            <Alert severity={responseMessage.type}>{responseMessage.text}</Alert>
          </DialogContent>
          <DialogActions>
            <Button onClick={() => handleCloseConfirmationDialog()} className="cancel-button">Cerrar</Button>
          </DialogActions>
        </Dialog>
      )}
    </>
  );
};

export default ModalActualizarCliente;
